import { Component, Input, OnInit } from '@angular/core';
import { Post } from '../../../models/post';
import { Catagory } from '../../../models/Catagory';
import { PostService } from '../../../services/post.service'
import { Observable } from 'rxjs';
import * as $ from 'jquery'; 

@Component({
  selector: 'fandom',
  templateUrl: './fandom.component.html',
  styleUrls: ['./fandom.component.css']
})
export class FandomComponent implements OnInit {

  @Input() post: Post;
  posts$: Observable<Post[]>
  catagory: string;
  order: boolean = false;
  sortBy: string = '';

  constructor(private postService: PostService) { }

  catagories: Catagory[] = [new Catagory('Articles','article','post'), new Catagory('Characters','character','post'), new Catagory('Events','event','post'), new Catagory('Geography','geography','post'), new Catagory('Maps','map','map'), new Catagory('Timelines','timeline','timeline')]

  ngOnInit(): void {
    this.postService.fetch(this.post.id).subscribe(
      data=>{
        this.post= data[0][0];  
        this.posts$ = this.postService.fetchAllOfFandom(this.post.id);
        this.postService.viewed(this.post.id).subscribe(
          response => console.log('success', response),
          error=> console.log('error', error)
        );
      }
    );
    this.searchList();
  }

  testReturn(test: string) {
    return test;
  }

  getImageURL(cat: string) {
    return '../../../../assets/catagory/'+cat+'.jpg';
  }

  getImage(imageStr: string) {
    return 'http://kronosfandomtimelines.com/' + imageStr;
  }

  searchList() {
    $(document).ready(function () {
      $("#search").on("keyup", function () {
        var value = $(this).val().toLowerCase();
        $("#list li").filter(function () {
          $(this).toggle($(this).text().toLowerCase().indexOf(value) > -1)
        });
      });
    });
  }

  sortByChange(e) {
    this.sortBy = e;
    this.posts$ = this.postService.fetchAllOfFandomAndSort(this.post.id, this.sortBy, this.order);
  }

  reorder() {
    if (this.order) {
      this.order = false;
      this.posts$ = this.postService.fetchAllOfFandomAndSort(this.post.id, this.sortBy, this.order);
    } else {
      this.order = true;
      this.posts$ = this.postService.fetchAllOfFandomAndSort(this.post.id, this.sortBy, this.order);
    }
  }
}