import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { CreateComponent } from '../../create.component';
import { FormBuilder, FormGroup } from '@angular/forms';
import { PostService } from '../../../../services/post.service'
import { ImageService } from '../../../../services/image.service'
import * as $ from 'jquery';
import { Router } from '@angular/router';

@Component({
  selector: 'post-create',
  templateUrl: './post-create.component.html',
  styleUrls: ['./post-create.component.css']
})
export class PostCreateComponent extends CreateComponent implements OnInit {
  
  @Input() id: number;
  @Input() fandomId: number;
  @Input() type: string;
  @Input() catagory: string;

  @ViewChild('fileInput', { static: false }) fileInput: ElementRef;
  selectedFile = null;
  uploadForm: FormGroup;
  defaultImage: string = '../../../../assets/no-image.png';
  fileName: File = null;

  images: any[];
  domain: string = 'http://kronosfandomtimelines.com/'
  finished: string = 'Create';

  constructor (protected formBuilder: FormBuilder, protected http: PostService, protected imageService: ImageService, protected router: Router) {
    super(formBuilder, http, imageService, router);
  }

  ngOnInit(): void {
    this.uploadForm = this.formBuilder.group({
      fandomId: [this.fandomId],
      media: [''],
      mediaCaption: ['media caption'],
      mediaAlt: ['media alt'],
      mediaCredit: ['media credit'],
      uploader: 1,
    });
    this.postForm = this.formBuilder.group({
      fandomId: this.fandomId,
      type: [this.type],
      catagory: [this.catagory],
      theme: ['default'],
      name: ['insert title'],
      description: ['insert short descriptions'],
      sections: this.formBuilder.array([]),
      references: this.formBuilder.array([]),
      updator: 1,
      creator: 1
    });
    if (this.id != -1) { 
      this.finished = 'Update';
      this.getData();
    }
    this.imageService.fetchAllOfFandom(this.fandomId).subscribe(
      data => this.images = data
    );
  }

  getData() {
    this.http.fetch(this.id).subscribe(
      data=>{
        this.post = data[0][0];  
        this.postForm.patchValue({     
          theme: this.post['theme'],
          name: this.post['name'],
          description: this.post['description'],
          updator: this.post['updator'],
          creator: this.post['creator']
        })
        this.post.data['sections'].forEach(value => {
          this.loadSection('sections', value);
        });
      }
    );
  }

  selectedImage(index: number) {
    const i = this.getSection('sections').at(index).get('media').value
    $('#imagePreview' + index).attr('src', this.domain + this.images[i].media);
    this.getSection('sections').at(index).patchValue({
      media: [this.images[i].media],
      mediaCaption: [this.images[i].mediaCaption],
      mediaAlt: [this.images[i].mediaAlt],
      mediaCredit: [this.images[i].mediaCredit]
    });
  }

  displayPopup(formId: string) {
    console.log(formId);
    document.getElementById(formId).style.display = "block";
  }

  closePopup(formId: string) {
    console.log(formId);
    document.getElementById(formId).style.display = "none";
  }

  onSubmit(value) {
    if (this.isNew()) {
      console.log("NEW: "+value)
      this.http.post(value).subscribe( 
        (res) => {
          console.log(res);
          if (this.type != 'fandom') {
            this.router.navigate(['/post', this.fandomId, 'fandom', this.fandomId, 'fandom', 0])
          } else {
            this.router.navigate(['/fandoms'])
          }
        },
        (err) => console.log(err)
      );
    } else {
      console.log("UPDATE: "+value)
      this.http.put(value, this.id).subscribe(
        (res) => {
          console.log(res);
          this.router.navigate(['/post', this.id, this.type, this.fandomId, this.catagory, 0]);
        },
        (err) => console.log(err)
      );
    }
  } 

  getImage(imageStr: string) {
    return 'http://kronosfandomtimelines.com/' + imageStr;
  }

  onFileSelect(event){
    this.previewImage(event);
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.uploadForm.get('media').setValue(file);
    }
  }

  previewImage(fileInput: any){
    if (fileInput.target.files && fileInput.target.files[0]) {
      var reader = new FileReader();
      reader.onload = function (e : any) {
        $('#imagePreview').attr('src', e.target.result);
      }
      reader.readAsDataURL(fileInput.target.files[0]);
    }
  }

  public onUploadSubmit() {
    const formData = new FormData();
    formData.append('media', this.uploadForm.get('media').value);
    formData.append('fandomId', this.uploadForm.get('fandomId').value);
    formData.append('mediaCaption', this.uploadForm.get('mediaCaption').value);
    formData.append('mediaAlt', this.uploadForm.get('mediaAlt').value);
    formData.append('mediaCredit', this.uploadForm.get('mediaCredit').value);
    formData.append('uploader', this.uploadForm.get('uploader').value);
    this.imageService.post(formData).subscribe(
      (res) => {
        console.log(res);
        this.imageService.fetchAllOfFandom(this.fandomId).subscribe(
          data => this.images = data
        );
        this.closePopup('popup');
      },
      (err) => console.log(err)
    );   
    
  }
}
